import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';

function init() {
	if(!VARIABLE.CONFIG.ANIMA_SCROLL) return;

	var scrollWrap = $('#scroll-view').get(0);
	
	VARIABLE.GLOBAL.SCROLL_OBJ = new anima_ctrl.scrollview(scrollWrap, {});
	
	VARIABLE.GLOBAL.SCROLL_OBJ.init();

	// 旋转设备时重新计算滚动区域尺寸
	$(window).bind('orientationchange', function() {
		VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
	});
}

module.exports = init;


