import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import Swiper from 'SWIPER_JS_PATH';

function main() {
	var swiper_box = $('.sec-tab');
	if(!swiper_box.length) return;

	var _scroll = VARIABLE.GLOBAL.SCROLL_OBJ;
	var view_scroll  = $('#scroller');

	var allLi = $('#sec-tab-wrap li');
	var liWidth = allLi.width();

	if(allLi.length * liWidth < view_scroll.width()) {
		$('.sec-tab-wrap li').css('width', 100/allLi.length + '%');
	}

	var navSwiperFix = new Swiper('#sec-tab-wrap-fix .swiper-container',{
		slidesPerView : 'auto',
		loop: false,
		preventClicks : true,
		freeMode: true,
		freeModeSticky: true,
		onClick: function(swiper) {

		},
		onInit: function(swiper) {
			var active_slider     = $(swiper.container[0]).find('.active').get(0);
			var swiper_width_half = parseInt(swiper.width / 2, 10);
			var offset_left       = active_slider.offsetLeft;
			var max_translate     = swiper.maxTranslate();
			var min_translate     = swiper.minTranslate();
			var translate_to      = swiper_width_half - offset_left - active_slider.offsetWidth / 2;

			if(translate_to > min_translate) {
				translate_to = min_translate;
			}else if(translate_to < max_translate) {
				translate_to = max_translate;
			}
			swiper.setWrapperTransition(0);
			swiper.setWrapperTranslate(translate_to);
		},
		onTransitionEnd: function(swiper) {
			var x = swiper.getWrapperTranslate();

			navSwiper.setWrapperTransition(0);
			navSwiper.setWrapperTranslate(x);
		}
	});

	var navSwiper = new Swiper('#sec-tab-wrap .swiper-container',{
		slidesPerView : 'auto',
		loop: false,
		preventClicks : true,
		freeMode: true,
		freeModeSticky: true,
		onClick: function(swiper) {

		},
		onInit: function(swiper) {
			var active_slider     = $(swiper.container[0]).find('.active').get(0);
			var swiper_width_half = parseInt(swiper.width / 2, 10);
			var offset_left       = active_slider.offsetLeft;
			var max_translate     = swiper.maxTranslate();
			var min_translate     = swiper.minTranslate();
			var translate_to      = swiper_width_half - offset_left - active_slider.offsetWidth / 2;

			if(translate_to > min_translate) {
				translate_to = min_translate;
			}else if(translate_to < max_translate) {
				translate_to = max_translate;
			}
			swiper.setWrapperTransition(0);
			swiper.setWrapperTranslate(translate_to);
		},
		onTransitionEnd: function(swiper) {
			var x = swiper.getWrapperTranslate();

			navSwiper.setWrapperTransition(0);
			navSwiper.setWrapperTranslate(x);
		}
	});

	var secTabWrap = $('#sec-tab-wrap');
	var _pading = parseFloat(secTabWrap.find('.sec-tab').css('padding-top'));

	if(_scroll) {
		_scroll.addScrollingHandler(function() {
			var y = _scroll.getScrollTop();

			if(y >= secTabWrap.offset().top - view_scroll.offset().top + _pading) {
				$('#sec-tab-wrap-fix').removeClass('sec-tab-wrap-fix');
			}else{
				$('#sec-tab-wrap-fix').addClass('sec-tab-wrap-fix');
			}
		});
	}
}

module.exports = main;


