import $ from 'ZEPTO_PATH';
var VARIABLE = require('UNIT_PATH/variable');

function main() {
	if(!window.sessionStorage) {
		return;
	}

	var _Scroll = VARIABLE.GLOBAL.SCROLL_OBJ;
	var _Lazy = VARIABLE.GLOBAL.LAZY;

	if(sessionStorage.getItem('index_y')) {
		if(VARIABLE.CONFIG.ANIMA_SCROLL) {
			_Scroll.scrollTo(parseInt(sessionStorage.getItem('index_y'), 10));
			_Lazy.refreshImg();
		}else{
			$(window).scrollTop(parseInt(sessionStorage.getItem('index_y'), 10));
		}
		sessionStorage.removeItem('index_y');
	};

	// 只是滚动区域区域
	$('#scroll-view').bind('click', function(ev) {
		var target = $(ev.target);
		var _a = target.closest('a');

		if(!_a.length) return;

		var _href = _a.attr('href');

		if(_a.find('.total-count').length) {
			_href = 'pass';
		}

		if(_href == '' || _href == 'javascript:;' || _href == '#') return;

		if(target.closest('.filter-wrap').length || target.closest('.red-nav-box').length || target.closest('.flexible-area').length || target.closest('.sec-tab').length) {
			return sessionStorage.removeItem('index_y');
		}

		if(VARIABLE.CONFIG.ANIMA_SCROLL) {
			window.sessionStorage.setItem('index_y', _Scroll.getScrollTop());
		}else{
			window.sessionStorage.setItem('index_y', $(window).scrollTop());
		}
	});
}

module.exports = main;


